/* HexGame.css */

.hex-game-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #111; /* Dark background color */
  }
  
  .hex-game {
    position: relative;
    background-color: #1a1a1a; /* Darker background color */
    padding: 20px;
    border-radius: 15px;
    box-shadow: 0 0 30px rgba(255, 255, 255, 0.1); /* Soft glow effect */
  }
  
  header h1 {
    font-size: 3em;
    color: #03dac6; /* Light teal color */
    margin-bottom: 20px;
  }
  
  section p {
    font-size: 1.2em;
    color: #fff; /* Light text color */
    margin-bottom: 20px;
  }
  
  .mine-button {
    padding: 15px 30px;
    font-size: 1.2em;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    background: linear-gradient(45deg, #03dac6, #6200ea); /* Gradient color */
    color: #fff; /* Light text color */
    transition: all 0.3s ease-in-out;
  }
  
  .mine-button:hover {
    transform: scale(1.1);
    background: linear-gradient(45deg, #6200ea, #03dac6); /* Gradient color on hover */
  }
  
  .total-coins {
    font-size: 1.5em;
    color: #03dac6; /* Light teal color */
    margin-top: 20px;
  }
  
  /* Animation for hex coins */
  @keyframes hexCoinsAnimation {
    0% {
      transform: translateY(0) rotate(0deg);
    }
    100% {
      transform: translateY(-300px) rotate(720deg);
      opacity: 0;
    }
  }
  
  /* Apply the animation to the hex coins */
  .hex-coin {
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: #f39c12; /* Hex coin color */
    border-radius: 50%;
    animation: hexCoinsAnimation 1s ease-out;
  }
  